import styled from "styled-components";
import { Title3, Title4 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";
import { css } from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";

const SHorizontalBox = styled.div`
  display: flex;
  justify-content: space-between;
  gap: 80px;
  grid-column: 1/3;

  overflow: hidden;

  box-sizing: border-box;
  width: 100%;
  max-width: 1700px;
  min-height: 640px;
  padding: 45px;

  @media (max-width: 1500px) {
    padding: 35px;
    border-radius: 33px;
  }

  text-align: left;

  border-radius: 53px;
  background: ${({ color }) => {
    switch (color) {
      case "blue":
        return `
          radial-gradient(68.55% 82.85% at 11.32% 115.16%, #36DBFF 0%, rgba(54, 219, 255, 0) 100%), #2D85F7;),
          #2d84f7`;

      case "green":
        return `
          radial-gradient(68.55% 82.85% at 11.32% 115.16%,#48DAFC 0%, rgba(54, 219, 255, 0) 100%), 
          #10ACE0;`;

      case "pink":
        return `
          radial-gradient(51.72% 161.8% at 97.16% -41.48%,#682A77 0%, rgba(104, 42, 119, 0) 100%), 
          radial-gradient(54.77% 56.72% at 11.32% 115.16%, #B13F67 0%, rgba(177, 63, 105, 0) 100%), 
          #2B2257;`;

      case "darkblue":
        return `
          radial-gradient(101.6% 186.88% at 97.16% -41.48%,#0C0A5D 0%, rgba(13, 10, 94, 0) 100%), 
          radial-gradient(54.77% 56.72% at 11.32% 115.16%, #6C2EDA 0%, rgba(108, 46, 218, 0) 100%), 
          #050347;`;

      case "darkviolet":
        return `
          radial-gradient(48.5% 50.08% at 31.84% 78.44%, rgba(142, 71, 236, 0.2) 0%, rgba(0, 0, 0, 0) 100%, 
          rgba(142, 71, 236, 0) 100%), 
          radial-gradient(101.6% 186.88% at 97.16% -41.48%, #8E47EC 0%, rgba(13, 10, 94, 0) 100%), 
          radial-gradient(49.08% 61.64% at 1.78% 133.91%, #DFA25F 0%, rgba(223, 162, 95, 0) 100%), 
          #1F1F1F;`;
    }
  }};
`;

const Desc = styled.div`
  max-width: 654px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;

  @media (max-width: 1300px) {
    max-width: 454px;
  }
`;

const Label = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const Name = styled(Title3)`
  font-weight: 400;
  font-size: 55px;
  line-height: 110%;
`;

const textStyle = css`
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
`;

const Title = styled(Title4)`
  font-weight: 400;
  font-size: 26px;
  line-height: 120%;
`;

const List = styled.ul`
  padding-left: 30px;
  max-width: 547px;
  color: white;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;

  & li {
    position: relative;
    ${textStyle}
  }

  & li::before {
    content: "";
    background-color: #ffffff55; /* Цвет маркера */
    width: 10px;
    height: 10px;
    position: absolute;

    top: 10px;
    left: -26px;
    border-radius: 100%;
  }
`;

const Socials = styled.div`
  display: flex;
  gap: 17px;
  margin-top: 6px;

  & svg {
    opacity: 0.5;
    width: 30px;
    height: 30px;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  gap: 10px;

  & a {
    width: 284px;
    background-color: #ffffff;
    border-radius: 50px;
  }
`;

const ProductView = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 15px;

  & img {
    max-width: 761px;
    max-height: 499.24px;

    margin-right: 0px;
  }

  @media (max-width: 1550px) {
    & img {
      width: 100%;
    }
  }

  @media (max-width: 1200px) {
    margin-right: -350px;
    & img {
      width: auto;
      height: 100%;
      aspect-ratio: 761 / 499;
    }
  }
`;

function HorizontalBox({
  boxcolor,
  btncolor,
  name,
  title,
  view,
  list,
  socials,
  href,
  language,
}) {
  return (
    <SHorizontalBox color={boxcolor}>
      <Desc>
        <Label>
          <Name>{name}</Name>
          <Title>{title}</Title>
          <List>
            {list &&
              list.map((list_item) => <li key={Math.random()}>{list_item}</li>)}
          </List>
        </Label>
        <Socials>{socials}</Socials>
        <ButtonBox>
          <a href={href} aria-label={"Узнать больше о продукте " + name}>
            <Button type="secondary" colorTxt={btncolor} link={true}>
              {language === "Ru" ? "Узнать больше" : EngLoc.buttons.more}
            </Button>
          </a>
        </ButtonBox>
      </Desc>
      <ProductView>
        <img loading="lazy" src={view} alt="Product View" />
      </ProductView>
    </SHorizontalBox>
  );
}

export default HorizontalBox;
